import style from './Test_item_one.module.css';
import drone from '../../../img/TD.png';






const Test_item_one = () => {
    return (

        <div className={style.test_main}>
            <div className='container'>
                <p><b>Mavic Pro</b> - представляет собой очень компактный, но при этом
                    мощный квадрокоптер. Анонс данной модели появился ещё в 2016 году,
                    а в 2018 появилась обновленная версия DJI Mavic 2 Pro. Первая версия
                    дрона получилась настолько хорошей, что по началу в магазинах
                    образовался дефицит устройств.
                </p>

                <div className={style.test_pokaz}>

                    <div className={style.pokaz_item}>
                        <div className={style.pokaz_one}>
                            <h3>Мощьность -</h3>
                            <div className={style.pokaz_line}>
                                <div style={{ "width": "50%" }} className={`${style.line_active} ${style.line_active_one}`}>
                                    <h4>50%</h4>
                                </div>
                            </div>

                        </div>
                        <div className={style.pokaz_one}>
                            <h3>Батарея -</h3>
                            <div className={style.pokaz_line}>
                                <div style={{ "width": "40%" }} className={`${style.line_active} ${style.line_active_two}`}>
                                    <h4>2000 мА/ч</h4>
                                </div>
                            </div>
                        </div>
                        <div className={style.pokaz_one}>
                            <h3>Дальность -</h3>
                            <div className={style.pokaz_line}>
                                <div style={{ "width": "30%" }} className={`${style.line_active} ${style.line_active_three}`}>
                                    <h4>30km</h4>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div  className={`${style.pokaz_item} ${style.pokaz_item_m}`}>
                        <div className={style.pokaz_one}>
                            <h3>Зум -</h3>
                            <div className={style.pokaz_line}>
                                <div style={{ "width": "40%" }} className={`${style.line_active} ${style.line_active_four}`}>
                                    <h4>4X</h4>
                                </div>
                            </div>

                        </div>
                        <div className={style.pokaz_one}>
                            <h3>Вес -</h3>
                            <div className={style.pokaz_line}>
                                <div style={{ "width": "80%" }} className={`${style.line_active} ${style.line_active_five}`}>
                                    <h4>895 г</h4>
                                </div>
                            </div>
                        </div>
                        <div className={style.pokaz_one}>
                            <h3>Высота П. -</h3>
                            <div className={style.pokaz_line}>
                                <div style={{ "width": "60%" }} className={`${style.line_active} ${style.line_active_six}`}>
                                    <h4>6 km</h4>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>

                <div className={style.stats}>

                    <div className={style.stats_drone}>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>

                        <div className={style.drone}>
                            <img src={drone} alt="" />
                        </div>
                    </div>

                    <div className={style.stats_pokazat}>
                        <div className={style.stats_line}>
                            <h4>10km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>20km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>30km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>40km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>50km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>60km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>70km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>80km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>90km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>100km</h4>
                            
                            <div></div>
                        </div>

                        
                    </div>
                </div>
            </div>
        </div>

    );
}

export default Test_item_one;


