import style from './Test_item_three.module.css';
import drone from '../../../img/TD.png';






const Test_item_three = () => {
    return (

        <div className={style.test_main}>
            <div className='container'>
                <p><b>Mavic 3</b> - Легендарный шведский бренд Hasselblad создал 
                для DJI Mavic 3 индивидуальную камеру L2D-20c, наделив ее профессиональной
                 матрицей 4/3 в невероятно компактном корпусе. Высокие стандарты Hasselblad
                  применялись как в работе над оборудованием, так и над алгоритмами ПО, 
                  что позволило вывести качество изображения на новый уровень.
                </p>

                <div className={style.test_pokaz}>

                    <div className={style.pokaz_item}>
                        <div className={style.pokaz_one}>
                            <h3>Мощьность -</h3>
                            <div className={style.pokaz_line}>
                                <div style={{ "width": "90%" }} className={`${style.line_active} ${style.line_active_one}`}>
                                    <h4>90%</h4>
                                </div>
                            </div>

                        </div>
                        <div className={style.pokaz_one}>
                            <h3>Батарея -</h3>
                            <div className={style.pokaz_line}>
                                <div style={{ "width": "80%" }}  className={`${style.line_active} ${style.line_active_two}`}>
                                    <h4>8000 мА/ч</h4>
                                </div>
                            </div>
                        </div>
                        <div className={style.pokaz_one}>
                            <h3>Дальность -</h3>
                            <div className={style.pokaz_line}>
                                <div style={{ "width": "70%" }} className={`${style.line_active} ${style.line_active_three}`}>
                                    <h4>70km</h4>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className={`${style.pokaz_item} ${style.pokaz_item_m}`}>
                        <div className={style.pokaz_one}>
                            <h3>Зум -</h3>
                            <div className={style.pokaz_line}>
                                <div style={{ "width": "80%" }} className={`${style.line_active} ${style.line_active_four}`}>
                                    <h4>8X</h4>
                                </div>
                            </div>

                        </div>
                        <div className={style.pokaz_one}>
                            <h3>Вес -</h3>
                            <div className={style.pokaz_line}>
                                <div style={{ "width": "50%" }} className={`${style.line_active} ${style.line_active_five}`}>
                                    <h4>595 г</h4>
                                </div>
                            </div>
                        </div>
                        <div className={style.pokaz_one}>
                            <h3>Высота П. -</h3>
                            <div className={style.pokaz_line}>
                                <div style={{ "width": "90%" }} className={`${style.line_active} ${style.line_active_six}`}>
                                    <h4>9 km</h4>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>

                <div className={style.stats}>

                    <div className={style.stats_drone}>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>

                        <div className={style.drone}>
                            <img src={drone} alt="" />
                        </div>
                    </div>

                    <div className={style.stats_pokazat}>
                        <div className={style.stats_line}>
                            <h4>10km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>20km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>30km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>40km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>50km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>60km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>70km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>80km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>90km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>100km</h4>
                            
                            <div></div>
                        </div>

                        
                    </div>
                </div>
            </div>
        </div>

    );
}

export default Test_item_three;


