import style from './Opis.module.css';
import imgOpis from '../../img/Opis.svg';


const Opis = () => {
    return (
        <div className="container" id='opis'>
            <div className={style.opis_content}>
                <div className={style.opis_item}>
                    <h2>Mavic 2</h2>
                    <p>мощный флагманский дрон с камерой, <br />
                        оснащенный камерой Hasselblad с CMOS-матрицей 4/3 <br />
                        для упрощения создания изображения <br />
                        профессионального уровня.
                    </p>
                </div>
                <div className={`${style.opis_item} ${style.img}`}>
                    <img src={imgOpis} alt="" />
                </div>
            </div>
        </div>
    );
}

export default Opis;


