import './nav.css';
import vk from '../../../img/vk.svg';
import tg from '../../../img/tg.svg';


const Burger = () => {



  return (


    <div class="hamburger-menu">
      <input id="menu__toggle" type="checkbox" />
      <label class="menu__btn" for="menu__toggle">
        <span></span>
      </label>

      <ul class="menu__box">
        <li><a class="menu__item" href="#">Главная</a></li>
        <li><a class="menu__item" href="#opis">Описание</a></li>
        <li><a class="menu__item" href="#test">Тест</a></li>

        <div class='menu_imgs'>
          <a class="img" href="https://vk.com/sasha_markin"><img src={vk} alt="" /></a>
          <a href="https://t.me/zuzaAlex"><img src={tg} alt="" /></a>
        </div>
      </ul>


    </div>



  );
}

export default Burger;


