import style from './Footer.module.css';
import vk from '../../img/vk.svg';
import tg from '../../img/tg.svg';
import f_drone from '../../img/f_drone.svg';





const Footer = () => {
    return (
       <footer>
            <div className='container'>
                <div className={style.f_content}>
                    <h2>DroneX</h2>
                    <img src={f_drone} alt="" />
                    <div className={style.f_imgs}>
                        <a className={style.btn_l} href="https://vk.com/sasha_markin"><img src={vk} alt="" /></a>
                        <a href="https://t.me/zuzaAlex"><img src={tg} alt="" /></a>
                    </div>
                </div>
            </div>
       </footer> 
    );
}

export default Footer;


