import style from './Prem.module.css';
import imgDrone from '../../img/Drone.svg';
import one from '../../img/1.png';
import two from '../../img/2.png';
import three from '../../img/3.png';
import four from '../../img/4.png';
import five from '../../img/5.png';
import six from '../../img/6.png';




const Prem = () => {
    return (
        <div className="container">
            <div className={style.prem_content}>
                <div className={`${style.prem_item} ${style.item_b}`}>
                    <img src={imgDrone} alt="" />
                </div>
                <div className={style.prem_item}>

                    <div className={style.prem_one}>
                        <div className={style.prem_block}>
                            <img src={one} alt="" />
                            <p>Ультралегкий <br />
                                и складной
                            </p>
                        </div>
                        <div className={style.prem_block}>
                            <img src={two} alt="" />
                            <p>Макс. время <br />
                                полета: 31 мин.</p>
                        </div>
                        <div className={style.prem_block}>
                            <img src={three} alt="" />
                            <p>Передача <br />
                                видеосигнала: 10км</p>
                        </div>
                    </div>

                    <div className={style.prem_one}>
                        <div className={style.prem_block}>
                            <img src={four} alt="" />
                            <p>Скорость <br />
                                ветра до 5 уровня
                            </p>
                        </div>
                        <div className={style.prem_block}>
                            <img src={five} alt="" />
                            <p>Сверхчеткие <br />
                                видео 4K</p>
                        </div>
                        <div className={style.prem_block}>
                            <img src={six} alt="" />
                            <p>Режимы <br />
                                быстрой съемки</p>
                        </div>
                    </div>
                    <div className={style.prem_btn}>
                        <a href="#test">Смотреть</a>
                    </div>
                </div>

            </div>

        </div>
    );
}

export default Prem;


