import style from './Test.module.css';
import Test_item_one from './Test_item_one/Test_item_one';
import Test_item_two from './Test_item_two/Test_item_two';
import Test_item_three from './Test_item_three/Test_item_three';
import { Route, Routes, NavLink } from 'react-router-dom';





const Test = () => {



    return (
        <div id='test' className={style.Test}>
            <div className='container'>
                <h2>Test</h2>
                <div className={style.test_btns}>
                    {/* <a className={style.test_btns_active}>Mavic-pro</a> */}
                    <NavLink className={({ isActive }) => (isActive ? `${style.test_btns_active}` : '')} exact  to='/'>Mavic-pro</NavLink>
                    <NavLink className={({ isActive }) => (isActive ? `${style.test_btns_active}` : '')} to='/item_two'>Mavic-2</NavLink>
                    <NavLink className={({ isActive }) => (isActive ? `${style.test_btns_active}` : '')} to='/item_three'>Mavic-3</NavLink>
                    {/* <a >Mavic-2</a> */}
                </div>
            </div>
            <Routes>
                <Route path='/' element={<Test_item_one />} />
                <Route path='/item_two' element={<Test_item_two />} />
                <Route path='/item_three' element={<Test_item_three />} />
            </Routes>

        </div>

    );
}

export default Test;


