import style from './Test_item_two.module.css';
import drone from '../../../img/TD.png';






const Test_item_two = () => {
    return (

        <div className={style.test_main}>
            <div className='container'>
                <p><b>Mavic 2</b> - мощный флагманский дрон с камерой,
                 оснащенный камерой Hasselblad с CMOS-матрицей 4/3 для 
                 упрощения создания изображения профессионального уровня. 
                 Устройство предлагает всенаправленное распознавание препятствий 
                 для оптимального опыта полета, максимальное время полета до 46
                 F минут и максимальную дальность передачи сигнала 15 км.
                </p>

                <div className={style.test_pokaz}>

                    <div className={style.pokaz_item}>
                        <div className={style.pokaz_one}>
                            <h3>Мощьность -</h3>
                            <div className={style.pokaz_line}>
                                <div style={{ "width": "70%" }} className={`${style.line_active} ${style.line_active_one}`}>
                                    <h4>70%</h4>
                                </div>
                            </div>

                        </div>
                        <div className={style.pokaz_one}>
                            <h3>Батарея -</h3>
                            <div className={style.pokaz_line}>
                                <div style={{ "width": "60%" }}  className={`${style.line_active} ${style.line_active_two}`}>
                                    <h4>4000 мА/ч</h4>
                                </div>
                            </div>
                        </div>
                        <div className={style.pokaz_one}>
                            <h3>Дальность -</h3>
                            <div className={style.pokaz_line}>
                                <div style={{ "width": "50%" }} className={`${style.line_active} ${style.line_active_three}`}>
                                    <h4>50km</h4>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className={`${style.pokaz_item} ${style.pokaz_item_m}`}>
                        <div className={style.pokaz_one}>
                            <h3>Зум -</h3>
                            <div className={style.pokaz_line}>
                                <div style={{ "width": "60%" }} className={`${style.line_active} ${style.line_active_four}`}>
                                    <h4>6X</h4>
                                </div>
                            </div>

                        </div>
                        <div className={style.pokaz_one}>
                            <h3>Вес -</h3>
                            <div className={style.pokaz_line}>
                                <div style={{ "width": "60%" }} className={`${style.line_active} ${style.line_active_five}`}>
                                    <h4>695 г</h4>
                                </div>
                            </div>
                        </div>
                        <div className={style.pokaz_one}>
                            <h3>Высота П. -</h3>
                            <div className={style.pokaz_line}>
                                <div style={{ "width": "80%" }} className={`${style.line_active} ${style.line_active_six}`}>
                                    <h4>8 km</h4>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>

                <div className={style.stats}>

                    <div className={style.stats_drone}>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>
                        <div className={style.drone_line}></div>

                        <div className={style.drone}>
                            <img src={drone} alt="" />
                        </div>
                    </div>

                    <div className={style.stats_pokazat}>
                        <div className={style.stats_line}>
                            <h4>10km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>20km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>30km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>40km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>50km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>60km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>70km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>80km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>90km</h4>
                            <div></div>
                        </div>

                        <div className={style.stats_line}>
                        <h4>100km</h4>
                            
                            <div></div>
                        </div>

                        
                    </div>
                </div>
            </div>
        </div>

    );
}

export default Test_item_two;


